header {
    border-bottom: 2px solid black;
    margin-bottom: 25px;
  }
  .flex-header {
      display: flex;
      flex-wrap: wrap;
    }
    .flex-header > .logo {
      flex:2;
      font-family: 'Open Sans', sans-serif;
      font-size: 300%;
      margin-left:20px;
      margin-bottom: 7px;
      color:rgb(200, 69, 52);
    }
    .flex-header > .menu {
      flex:1;
    }
    
    .flex-header > .menu > nav > ul{
      display:flex;
      align-content:center;
      list-style-type:none;
      margin: 5px;
      padding: 10px;
      flex-direction: row-reverse;
      justify-content: space-evenly;
      text-align: center;
    }
  
    .flex-header > .menu > nav > ul > li{
      flex:1;
    }
   
    .flex-header > .menu > nav > ul > li > a{
      padding:10px;
      transition: background-color 0.5s, font-size 0.5s;
      border:1px rgba(0, 0, 0, 0.5) solid;
      border-radius: 25px;
      color:black;
      text-decoration: none;
      line-height: 200%;
    }
  
    .flex-header > .menu > nav > ul > li > a:hover{
      background-color:rgb(200, 69, 52) ;
      border:1px rgba(0, 0, 0, 0.5) solid;
      border-radius: 25px;
      padding:10px;
      font-size: 125%;
  
    }
  